import React from "react";
import { useRef } from "react";
import "./home.css";
import Login from "../../componentes/login/login";
import LogoHeader from "../../assets/grana-logo-1.png";
import DropDown from "../../assets/drop-down.png";
import Whatsapp from "../../componentes/whatsapp/whatsapp";
import WhatsappImg from "../../assets/wpp-white.png";
import { useHistory } from "react-router-dom";
import Modal from "react-modal";
import BannerPhoto from "../../assets/home-banner-photo.png";
import Footer from "../../componentes/footer/footer";

export default function Home() {
  const [modalIsOpen, setModalIsOpen] = React.useState(false);
  const history = useHistory();

  const showModal = () => {
    setModalIsOpen(true);
  };

  const hideModal = () => {
    setModalIsOpen(false);
  };

  const scrollRef = useRef(null);

  const scroll = () => {
    scrollRef.current.scrollIntoView();
  };

  return (
    <div className="Home">
      <header>
        <img src={BannerPhoto} alt="banner" />
        <div className="header-container">
          <div className="header-top">
            <nav>
              <img src={LogoHeader} alt="logo" />
              <span onClick={showModal}>Login</span>
              <Modal
                isOpen={modalIsOpen}
                className="Modal"
                overlayClassName="Overlay"
              >
                <div className="close">
                  <button onClick={hideModal}>x</button>
                </div>
                <Login />
              </Modal>
            </nav>
            <h1>Investimento com impacto social</h1>
          </div>

          <div className="header-bot">
            <hr></hr>
            <a onClick={() => {
              history.push(`${process.env.PUBLIC_URL}/registro-sonhador`);
            }}>Preciso de investimento</a>
            <a onClick={() => {
              history.push(`${process.env.PUBLIC_URL}/registro-apoiador`);
            }}>Quero investir com propsito</a>

          </div>
        </div>
      </header>

      <div className="home-content">
        <div className="home-container">
          <div className="about-container" ref={scrollRef}>
            <h3>Ns somos a</h3>
            <h2>Grana Solidria</h2>
            <p>
            Conectamos pessoas que querem investir com propsito a empreendedores qualificados. Esta qualificao
            ser realizada por meio da nossa plataforma <strong>"trilha do sucesso"</strong> e por nossos parceiros
            especialistas em educao empreendedora.  Queremos criar um ecossistema capaz de prover, de forma simultnea
            e integrada, crdito e educao empreendedora.
            </p>
          </div>

          <div className="image-container">
            <div className="dreamer-container">
              <div className="overlay"></div>
              <div className="text-container">
                <div className="text-tile">
                  <h3>Quem  o empreendedor</h3>
                  <h2>da Grana Solidria</h2>
                </div>

                <div className="description">
                  <p>
                    Uma pessoa que busca uma soluo que alie crdito e educao empreendedora
                    em negcios conectados a valores sociais e ambientais.
                    {/* Necessario correo e adio. */}
                  </p>
                  <button onClick={() =>{
					  history.push(`${process.env.PUBLIC_URL}/registro-sonhador`)
				  }}>Preciso de investimento</button>
                </div>
              </div>
            </div>

            <div className="supporter-container">
              <div className="overlay-supporter"></div>

              <div className="text-container">
                <div className="text-tile">
                  <h3>Quem investe nos empreendedores</h3>
                  <h2>da Grana Solidria</h2>
                </div>

                <div className="description">
                  <p>
                  O <strong>Investidor</strong>  a fora que move as engrenagens da Grana Solidria. Ele deseja colocar o seu
                  dinheiro em algo que v alm de uma simples aplicao financeira. Ele quer ver o investimento
                  render com propsito e impactar positivamente na vida das pessoas, melhorando tambm o nosso
                  planeta atravs da gerao de trabalho e renda e de aes que preservem o meio-ambiente.
                  </p>

                  <button onClick={() =>{
					  history.push(`${process.env.PUBLIC_URL}/registro-apoiador`)
				  }}>Quero investir com propsito</button>
                </div>
              </div>
            </div>
          </div>

          <div className="how-it-works-container">
            <h2>Como funcionamos</h2>
            <ol>
              <li>
                O <strong>empreendedor</strong> entra na plataforma e preenche dados cadastrais bsicos,
                sem custo. Ao ser aprovado na anlise de situao cadastral, ele passa
                instantaneamente para a segunda etapa, onde far o cadastro completo para
                o score financeiro e o socioambiental, este ltimo exclusivo da Grana
                Solidria.
              </li>
              <li>
                O projeto, ento, entra no ranking para investimento e fica disponvel para
                captar <strong>Investidores</strong> por at 60 dias.
              </li>
              <li>
                O <strong>Investidor Pessoa Fsica ou Jurdica</strong> pode sinalizar o desejo de investir
                o valor que quiser em quantos projetos desejar.  possvel, inclusive,
                financiar projetos de forma integral, porm nossa plataforma sempre
                incentiva a pulverizao do investimento.
              </li>
              <li>
                Sendo o investidor um Fundo ou uma empresa que direcione um montante
                para investimento, a plataforma distribui o investimento pelo ranking.
              </li>
              <li>
                Quando a nossa plataforma faz o chamado matching - o encontro de
                <strong>Empreendedor e Investidor(es)</strong> inicia-se o processo de materializao do
                projeto atravs dos nossos parceiros financeiros. <strong>Empreendedor e
                Investidor</strong> abrem conta digital e  emitida, ento, a CCB (Cdula de Crdito
                Bancrio). Tambm  feito, para todos os <strong>Empreendedores</strong> habilitados a
                receber recursos, um seguro prestamista que garante o retorno do restante
                do valor principal emprestado, a qualquer tempo do contrato de
                financiamento, no caso de invalidez ou morte do Empreendedor.
              </li>
              <li>
                <strong>Empreendedores</strong> 100% adimplentes, ao final de cada ciclo de
                financiamento dentro da plataforma, so melhor colocados dentro do ranking
                da Grana Solidria ao pedir novos investimentos.
              </li>
              <li>
                Empreendedores que se tornarem tambm Investidores, ao final de cada
                ciclo de financiamento, tambm recebem melhor posicionamento no ranking
                de investimento da Grana Solidria.
              </li>
            </ol>

            <div className="center">
              <span>Indique a Grana Solidaria.</span>
            </div>
          </div>
        </div>
      </div>
      <Whatsapp />
      <Footer />
    </div>
  );
}
