import "./how-it-works.css";
import Header from "../../componentes/header/header";
import Footer from "../../componentes/footer/footer";

export default function HowItWorks() {
  return (
    <div className="HowItWorks">
      <Header h1="Grana Solidria" h2="sobre a" menu="true" arrow="true" />

      <div className="about-content">
        <div className="container">
          <h2>Como funciona</h2>
          <p>
            O <strong>Sonhador</strong> nos conta qual empreendimento pessoal
            quer desenvolver ou melhorar. Uma pessoa que faz bolos, por exemplo,
            e quer fazer disso uma fonte de renda, pode encontrar aqui pessoas
            que desejam apoiar financeiramente o seu projeto, os chamados{" "}
            <strong>Apoiadores</strong>.
          </p>
          <p>
            O <strong>Apoiador</strong> escolhe quais projetos - ou sonhos -
            gostaria de ajudar a realizar e informa o valor que quer investir em
            cada um deles. A Grana Solidria incentiva que o{" "}
            <strong>Apoiador</strong> dilua o seu investimento em vrios
            projetos para diminuir o risco e, tambm, possibilitar que se crie
            uma rede onde vrios sonhos possam se concretizar.
          </p>
          <p>
            Quando a Grana Solidria encontra essa conexo, a sua rede de
            parceiros  acionada para materializar tudo. Um parceiro autorizado
            pelo Banco Central a fazer o emprstimo de pessoa para pessoa - de
            acordo com a Resoluo do Banco Central 4.656 de 28/04/2018 que
            regula as SEP - Sociedades de Emprstimo entre Pessoas - efetua a
            transao financeira que faz chegar aos fornecedores o dinheiro
            necessrio para realizar os sonhos. Enquanto outros parceiros
            capacitam os <strong>Sonhadores</strong> para empreender. Para
            colocar o seu pequeno negcio para rodar.
          </p>
          <p>
            Durante os cinco primeiros meses de sonho materializado, o{" "}
            <strong>Sonhador</strong>
            no paga nada aos <strong>Apoiadores</strong>. Esse  o perodo de
            carncia necessrio paagando aos <strong>Apoiadores</strong>.
            Durante todo o ciclo de 20 meses, ns da Grana Solidria monitoramos
            o andamento dos empreendimentos pessoais dos{" "}
            <strong>Sonhadores</strong>, mediante indicadores presentes no nosso
            sistema, atuando junto a eles em eventuais correes que ajudem na
            obteno do sucesso dos sonhos.
          </p>
          <p>
            A cada pagamento, o <strong>Apoiador</strong> recebe de nosso
            parceiro financeiro o valor acrescido de juros pago pelos{" "}
            <strong>Sonhadores</strong>
            e, caso deseje, pode sinalizar que quer reinvestir em novos
            projetos.
          </p>
          <p>
            J o <strong>Sonhador</strong>, ao final do ciclo de 20 meses, pode
            tornar-se tambm
            <strong>Apoiador</strong> ou novamente se inscrever como{" "}
            <strong>Sonhador</strong>
            para novos negcios ou a melhoria do seu empreendimento.
          </p>
          <h2>
            Informaes essenciais para o <strong>Apoiador</strong>
          </h2>
          <p>
            Ao se tornar <strong>Apoiador</strong> de projetos pela Grana
            Solidria o investidor fica ciente de que no h garantia de
            pagamento por parte do <strong>Sonhador</strong>.
          </p>

          <p>
            Porm, para se tornar <strong>Sonhador</strong>, cada empreendedor
            tem seu crdito avaliado pela Neurotech e  tambm treinado pelos
            nossos parceiros para gerir o seu empreendimento que tem todo o
            ciclo acompanhado por nossa plataforma visando o sucesso do negcio
            e, com isso, o pagamento do dinheiro emprestado e do lucro sobre o
            valor investido.
          </p>

          <p>
            A cada parcela paga o dinheiro j torna-se disponvel para saque via
            nosso parceiro bancri
          </p>

          <p>
            No h cota mnima nem mxima de investimento por projeto. Cada
            sonho fica disponvel por 2 meses para investimento e caso o valor
            necessrio para o empreendimento no seja atingido ele retorna para
            o <strong>Apoiador</strong> sem correo.
          </p>
        </div>
      </div>

      <Footer />
    </div>
  );
}
